'use client'
import Image from "next/image";
import { Card, CardContent, CardHeader, CardTitle } from "../ui/card"
import { useState } from "react";

const TableProducts = ({product}) => {
  const [isExpansed, setExpansed] = useState(false)

  const toggleText = ()=>{
    setExpansed(!isExpansed)
  }

  return (
    <Card className="bg-slate-800 h-96 w-56 border-0 rounded-none text-white">
        <CardContent className="px-1">
            <Image className="w-full h-56 object-cover" src={product.image} width={700} height={700} 
            alt={product.title}/>
        </CardContent>

        <CardHeader className="px-2">
            <CardTitle className="w-72 flex">
            <p className="w-44">
              {isExpansed || product.title.length <= 25
                  ? product.title
                  : `${product.title.slice(0, 15)}`
              }
            </p>
            <span className="w-10 flex justify-start">{product.title.length > 25 && (
            <button 
                onClick={toggleText} 
                className="text-zinc-400 hover:underline text-xs mt-1 inline"
            >
                {isExpansed ? "..." : "..."}
            </button>
            )}</span>
            </CardTitle>
            
        </CardHeader>
        
    </Card>
  )
}

export default TableProducts